<!-- 部门列表 -->
<template>
    <div class="page-body flex-column">
        <!-- 条件筛选区域 -->
        <div class="search-box">
            <el-button :size="'mini'" type="warning">导 出</el-button>
            <el-button :size="'mini'" type="primary" @click="addRow">添加部门</el-button>
        </div>

        <!-- 表格区域 -->
        <div class="table-box flex-1 shadow-box" ref="tableBox">
        <el-table :data="tableData" :max-height="tableBoxHeight_" :size="'small'" stripe border>
            <el-table-column v-for="(item, i) in tableConfig" :key="i" :fixed="item.fixed" :prop="item.prop" :label="item.label" :min-width="item.width"></el-table-column>
            <el-table-column fixed="right" label="操作" min-width="60">
                <template slot-scope="scope">
                    <el-button @click.native.prevent="editRow(scope.row)" type="text" size="mini">编辑</el-button>
                    <el-button @click.native.prevent="deleteRow(scope.row)" type="text" size="mini" style="color:#F56C6C">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        </div>
        <!-- 新增/编辑 弹窗 -->
        <Dialog ref="dialog" />
    </div>
</template>

<script>
import { tableContentAutoResize } from '../../utils/mixins'
import Dialog from './Dialogs/DialogDepartment'

const tableConfig = [
  { prop: 'a', label: 'ID', width: 60, fixed: true },
  { prop: 'b', label: '部门名称', width: 75, fixed: false },
  { prop: 'c', label: '部门标识', width: 75, fixed: false },
  { prop: 'd', label: '管理员', width: 75, fixed: false },
  { prop: 'e', label: '代理人数', width: 75, fixed: false },
  { prop: 'f', label: '注册人数', width: 75, fixed: false },
  { prop: 'g', label: '开通时间', width: 120, fixed: false },
  { prop: 'h', label: '上月收入', width: 75, fixed: false }
]

export default {
  mixins: [tableContentAutoResize],
  data () {
    return {
        tableConfig,
        tableData: [
            { a: 'aa1', b: 'bbb', c: 'ccc', d: 'ddd', e: 'eeee', f: 'fffff', g: 'ggggggg' },
            { a: 'aa2', b: 'bbb', c: 'ccc', d: 'ddd', e: 'eeee', f: 'fffff', g: 'ggggggg' },
            { a: 'aa3', b: 'bbb', c: 'ccc', d: 'ddd', e: 'eeee', f: 'fffff', g: 'ggggggg' },
            { a: 'aa4', b: 'bbb', c: 'ccc', d: 'ddd', e: 'eeee', f: 'fffff', g: 'ggggggg' },
            { a: 'aa5', b: 'bbb', c: 'ccc', d: 'ddd', e: 'eeee', f: 'fffff', g: 'ggggggg' },
            { a: 'aa6', b: 'bbb', c: 'ccc', d: 'ddd', e: 'eeee', f: 'fffff', g: 'ggggggg' },
            { a: 'aa7', b: 'bbb', c: 'ccc', d: 'ddd', e: 'eeee', f: 'fffff', g: 'ggggggg' },
            { a: 'aa8', b: 'bbb', c: 'ccc', d: 'ddd', e: 'eeee', f: 'fffff', g: 'ggggggg' },
            { a: 'aa9', b: 'bbb', c: 'ccc', d: 'ddd', e: 'eeee', f: 'fffff', g: 'ggggggg' },
            { a: 'aa0', b: 'bbb', c: 'ccc', d: 'ddd', e: 'eeee', f: 'fffff', g: 'ggggggg' },
            { a: 'aa11', b: 'bbb', c: 'ccc', d: 'ddd', e: 'eeee', f: 'fffff', g: 'ggggggg' },
            { a: 'aa12', b: 'bbb', c: 'ccc', d: 'ddd', e: 'eeee', f: 'fffff', g: 'ggggggg' }
        ]
    }
  },
  components: {
      Dialog
  },
  methods: {
      // 添加
      addRow () {
          this.$refs.dialog.open()
      },
      // 编辑
      editRow (row) {
          console.log(row)
          this.$refs.dialog.open(row)
      },
      // 删除
      deleteRow (row) {
        this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          console.log(row)
        }).catch(() => {})
      }
  }
}
</script>