<!-- 添加/编辑 部门弹窗 -->
<template>
    <el-dialog :title="title" :visible.sync="show" width="40rem">
    <el-form :model="form">
        <el-form-item size="mini" label="部门名称" :label-width="formLabelWidth">
            <el-input size="mini" v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item size="mini" label="部门标识" :label-width="formLabelWidth">
            <el-input size="mini" v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item size="mini" label="管理者" :label-width="formLabelWidth">
            <el-input size="mini" v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item size="mini" label="登录密码" :label-width="formLabelWidth">
            <el-input type="password" size="mini" v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button size="mini" @click="show = false">取 消</el-button>
        <el-button size="mini" type="primary" @click="show = false">确 定</el-button>
    </div>
    </el-dialog>
</template>

<script>
export default {
    data () {
        return {
            title: '',
            show: false,
            form: {

            },
            formLabelWidth: '8rem'
        }
    },
    methods: {
        open (datas) {
            this.show = true
            if (datas) { // 编辑
                this.title = '编辑部门'
            } else { // 添加
                this.title = '添加部门'
            }
        }
    }
}
</script>